/* ============================================= */
/*              Navigation                       */
/* ============================================= */

.nav {
    padding-left: 0;
    flex-basis: 100%;
    display: flex;
    flex-direction: column;

    @include mq ('md') {
        flex-direction: row;
        justify-content: flex-end;
        align-items: center;
    }
}

.nav-link {
    text-align: center;
    border-radius: 5px;
    padding: 10px 0;
    display: block;
    width: 100%;
    transition:
        background-color .2s ease-in-out,
        color .2s ease-in-out;

    @include mq ('md') {
        flex-basis: 100px;
    }
}

.nav-link:hover {
    color: #fff;
    background-color: #58e1c1;
}